import React from 'react';


class Footer extends React.Component {
  render() {
		
  	return (
      <div>
    		<section className="p-0">
           <div className="container-fluid">
           <div className="bottom-section">
              <div className="row">
                 <div className="col-md-6 p-0">
                    <div className="address-bar">
                       <div>
                          <ul className="footer-style">
                             <li>
                                <div className="footer-icon">
                                   <img src="assets/images/locate.png" alt="locate" />
                                </div>
                                <div className="footer-address">
                                   <a href={null}>Singapore</a>
                                </div>
                             </li>
                             {/* <li>
                                <div className="footer-icon">
                                   <img src="assets/images/telephone.png" alt="telephone" />
                                </div>
                                <div className="footer-address">
                                    <a href={null}>122-456-7890  /  124-789-5690</a>
                                </div>
                             </li> */}
                             <li>
                                <div className="footer-icon">
                                   <img src="assets/images/fotter-email.png" alt="fotter-email" />
                                </div>
                                <div className="footer-address">
                                    <a href={null}>support@infinitefrontier.sg</a>
                                </div>
                             </li>
                             {/* <li>
                                <div className="footer-icon">
                                   <img src="assets/images/fax.png" alt="" />
                                </div>
                                <div className="footer-address">
                                    <a href={null}>123-456  /  456-789</a>
                                </div>
                             </li> */}
                          </ul>
                       </div>
                    </div>
                 </div>
                 <div className="col-md-6 p-0">
                    <iframe title="google-map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d255281.19036261606!2d103.70416476090679!3d1.314339379599432!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31da11238a8b9375%3A0x887869cf52abf5c4!2sSingapore!5e0!3m2!1sen!2ssg!4v1542561434158" allowFullScreen className="md-height"></iframe>
                 </div>
              </div>
           </div>
           </div>
        </section>

        {/* <div className="copyright-section index-footer">
           <p>2018-19 copyright by themeforest powered by pixel strap</p>
        </div> */}

        <div className="tap-top">
           <div>
              <i className="fa fa-angle-double-up"></i>
           </div>
        </div>
      </div>
  	);
  }
}


export default Footer;


