import React from 'react';
import OwlCarousel from 'react-owl-carousel';


class Price extends React.Component {
  render() {
    // OwlCarousel Option for Prices
    const options = {
        0:{
            items:1,
            dots:true,
            margin:5,
        },
        600:{
            items:1,
            dots:true,
            margin:0,
        },
        768:{
            items:2,
            dots:true,
        },
        992:{
            items:3,
            dots:true,
        },
        1000:{
            items:3,
            dots:true,
        }
    };

    // Dynamic Price Data Easy to Update
    let data = [
        {
            title: 'Wedding Animation',
            // lable:'Only the basic features',
            price:'800++ SGD',
            features:'<li>High Quality Animated Short Story</li><li>Personalized Character Design (up to 3x)</li><li>Two personalized outfit for each, bride and groom</li><li>Customized Background Design for each scene</li><li>Sound FX and Background Music</li><li>Full HD</li>',
            link:'#'
        },
        {
            title: 'Save the date',
            lable:'250SGD for exclusive',
            price:'150SGD',
            features:'<li>Short Animation Montage</li><li>Personalized Character Design (2x)</li><li>Customized Background Design</li><li>Optimized for social media</li>',
            link:'#'
        },
        {
            title: 'E-Invitation (GIF)',
            // lable:'150SGD for exclusive',
            price:'150SGD',
            features:'<li>Short Animation Montage</li><li>Personalized Character Design (2x)</li><li>Customized Background Design</li><li>Optimized for social media</li>',
            link:'#'
        },
        {
            title: 'Photo Montage',
            // lable:'200SGD for exclusive',
            price:'200SGD',
            features:'<li>Epic digital effects</li><li>Minimum 20 photos</li><li>Multi purpose</li><li>Ful HD</li>',
            link:'#'
        },
    ];

    // Dynamic Price Data Loop
    let DataList = data.map((val, i) => {
        return (
          <div className="price-item" key={i}>
            <div className="price-block">
               <div className="price-type">
                  <h2>{val.title}</h2>
               </div>
               <div className="mrp">
                    {
                        val.lable ?
                        <h6 className="user-type">{val.lable}</h6>
                        : null
                    }
                  <div className="price-devide"></div>
                  <h2>{val.price}</h2>
                  <div className="price-devide"></div>
               </div>
               <ul className="price-feature" dangerouslySetInnerHTML={ {__html: val.features} } style={{ textAlign: 'left' }}></ul>
               <a href={val.link} className="btn btn-custom theme-color" role="button" >Select Plane</a>
            </div>
         </div>
        );
    });
		
  	return (
  		<section id="price" className="price">
           <div className="container">
              <div className="row">
                 <div className="col-sm-12">
                    <OwlCarousel
                        className="price-carousel owl-carousel owl-theme"
                        loop={true}
                        margin={30}
                        nav={false}
                        dots={false}
                        responsive={options}
                    >
                       {DataList}
                    </OwlCarousel>
                 </div>
              </div>
           </div>
        </section>
  	);
  }
}


export default Price;


